import React, { Component } from 'react';
import PropTypes from 'prop-types';

import './Department.css';

/**
 * essi component tti a vera einfaldur  birtingu en taka fall fr foreldri
 * sem keyrir egar smellt er  fyrirsgn.
 */

export default class Department extends Component {

  state = {
    title: PropTypes.string,
    tests: PropTypes.object,
    visible: PropTypes.bool,
    onHeaderClick: PropTypes.func,
    loading: true,
    error: false,
  };

  static defaultProps = {
    visible: true,
    onHeaderClick: () => { },
  }

  render() {    
    const { title, tests, visible, onHeaderClick, loading, error } = this.props;
    
    if (loading) {
      return (
        <div>Ski ggn</div>
      )
    }
    if (error) {
      return (
        <div>Villa a ggn</div>
      )
    }
    
      return (
        <div className="department">
          {!visible && (<h3 onClick={onHeaderClick}>+ {title}</h3>)}
          {visible && (
           <div>
           <h3 onClick={onHeaderClick}>- {title}</h3>
            <table>
              <thead>
                <tr>
                <th>Aukenni</th>
                <th>Nmskei</th>
                <th>Fjldi</th>
                <th>Dagsetning</th>
                </tr>
              </thead>
              <tbody>
                {tests.map((i) => {
                  return(
                    <tr>
                      <td>{i.course}</td>
                      <td>{i.name}</td>
                      <td>{i.students}</td>
                      <td>{i.date}</td>
                    </tr>
                  )
                  
                })}
              </tbody>
            </table>
            </div>
        )}            
        </div>
      );

  }
}



